<template>
    <div class="home">
        <WzCell v-for="(item,index) in list" 
        :key="index"
        :data="item"
        :dataName="'home'"
        ></WzCell>
    </div>
</template>


<script>
import WzCell from '../components/wz-cell'
export default {
    components:{
        WzCell
    },
    data() {
        return {
            list:[]
        }
    },
    mounted(){
        this.$axios({
            url:"/api/home",
            params:{
                _limit:10
            }
        }).then(
            res=> {
                this.list = res.data.data;
            }
        )
        this.$root.top = document.documentElement.scrollTop;

    },
    updated(){
        document.documentElement.scrollTop = this.$root.top; 
    }
}
</script>

<style lang="scss" scoped>
    .home{
        margin-top: .94rem;
    }
</style>