<template>
    <div class="reg">
        <Back></Back>
        <a href="#" class="logo"><img src="../assets/img/logo.png" /></a>
        <p class="p1"><span>头条</span></p>
        <van-cell-group class="list">
        <van-field
            v-model="username"
            required
            clearable
            label="用户名"
            right-icon="question-o"
            placeholder="请输入用户名"
        />

        <van-field
            v-model="password"
            type="password"
            label="密码"
            placeholder="请输入密码"
            required
        />
        </van-cell-group>
        <WzButton :value="'注册'" @click.native="reg" ></WzButton>
        <routerLink tag="a" to="/login"  href="javascript:;" class="zhuce">去登录</routerLink>
    </div>
</template>

<script>
import WzInput from '../components/wz-input'
import WzButton from '../components/wz-button'
import Back from "../components/back"
export default {
    components:{
        WzInput,WzButton,Back
    },
    data() {
        return {
            username:"",
            password:""
        }
    },
    methods: {
        reg() {
            let params = new URLSearchParams();
            params.append("username",this.username)
            params.append("password",this.password)
            this.$axios({
                url:"/api/reg",
                data:params,
                method:"post"
            }).then(
                res=>{
                    if(res.data.err == 0){
                        console.log(res.data);
                        this.$router.push("/login");
                    }else{
                        alert(res.data.msg)
                    }
                }
            )
        }
    },
}
</script>


<style lang="scss" scoped>
    .reg{text-align:center;height:100%;overflow:hidden;padding:0 .58rem;padding-bottom:5.58rem;}
    .reg .logo{display:block;height:1.44rem;width:1.47rem;margin:0 auto;margin-top:1.2rem;}
    .reg .logo img{display:block;height:1.44rem;width:1.47rem;}
    .reg .p1{color:#424242;font-size: .56rem;line-height: 2.01rem;}
    .reg .p1 span{color:#f88302;}
    .reg .dl{margin-top:1.55rem;text-indent:.34rem;font-size:.44rem;width:100%;display:block;height:1.17rem;border:0;border-top:.01rem solid #dcdcdc;background:#32b4ff;border-radius:0.58rem;color:#fff;}
    .reg .zhuce{font-size:.28rem;margin-top: .48rem;}
</style>